import $ from 'ZEPTO_PATH';
import Swiper from 'SWIPER_JS_PATH';
import VARIABLE from 'UNIT_PATH/variable';

function brandSliderInit() {
    if(!$('#best-brand li').length) return;

	var slider_config = {};

    if($('#best-brand li').length > 1) {
    	slider_config.loop = true;
    	slider_config.autoplayDisableOnInteraction = false;
    	slider_config.autoplay = 50000;
    	slider_config.pagination = '.brand-pagination';
    }else{
    	$('.brand-wraper').css('margin-top', 0);
    }

    var sliderSwiper = new Swiper('#best-brand .swiper-container', slider_config);
}

module.exports = brandSliderInit;


