import $ from 'ZEPTO_PATH';
var VARIABLE = require('UNIT_PATH/variable');

function init() {
	var priceTxt = $('.top-price .price-txt');
	var imgIndex = [1, 2, 3];
	var activeDom;

	run();
	setInterval(run, 3000);

	function run() {
		var allImgs = $('#top-imgs img');

		imgIndex.push(imgIndex.shift());
		allImgs.removeClass('active');
		activeDom = $('#top-imgs .top' + imgIndex[1]);
		activeDom.addClass('active');

		if(VARIABLE.CONFIG.PRICE_SWITCH != 'off') {
    		priceTxt.html(activeDom.attr('data-price'));
    	}
	}
}

module.exports = {
	init: init
}


