import $ from 'ZEPTO_PATH';
import cookie from 'UTILS_PATH/cookie';

function floatAd() {
	if(!cookie('floatAdUrl')) {
		if($('.float-ad').length) {
			_cookieSet();
		}
	}else{
		var _src = cookie('floatAdUrl');
		var dataSrc = $('.float-ad').find('img').attr('data-src');

		if(_src != dataSrc) {
			_cookieSet();
		}
	}
}

function _cookieSet() {
	var _time = parseInt($('.float-ad').find('img').attr('time-left'), 10);
	var _src = $('.float-ad').find('img').attr('data-src');
	var img = new Image();
	var date = new Date();
	var timeStart = date.getTime();
	var timeEnd = timeStart + _time;

	date.setTime(timeEnd);
	img.onload = function() {
		$('.float-ad').find('img').attr('src', _src);
		$('.float-ad').removeClass('none').find('.ad-con').removeClass('op0');
	}

	img.src = _src;
	$('.float-ad').find('.close-btn').bind('click', function() {
		$('.float-ad').remove();
	});
	cookie('floatAdUrl', _src, {expires: date});
}

module.exports = floatAd;


