import $ from 'ZEPTO_PATH';
import Swiper from 'SWIPER_JS_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import updateSlider from 'UNIT_PATH/updateSlider';

function main() {
	var this_swiper  = $('#filter-wrap');

	if(!this_swiper.length) return;

	var allLi = $('#filter-wrap li');
	var liWidth = allLi.width();
	var scroll = $('#scroller');

	if(allLi.length * liWidth < scroll.width()) {
		$('#filter-wrap li').css('width', 100/allLi.length + '%');
	}

	var navSwiper = new Swiper('#filter-wrap',{
		slidesPerView : 'auto',
		loop: false,
		preventClicks : true,
		freeMode: true,
		freeModeSticky: true,
		onClick: function(swiper) {

		},
		onInit: function(swiper) {
			var active_slider = $('.swiper-slide.active', this_swiper).get(0);

			if(active_slider) {
				filter_slide_to(swiper, active_slider, this_swiper, 0);
			}
		}
	});

	filterFixed();
}

function filterFixed() {
	var scroller_view = $('#scroll-view');
	var filter = $('#filter-wrap');
	var itemContent = $('#item-content');
	var firstItem = $('.item').eq(0);
	var body_dom = $('body');
	var top, filterTop;
	
	$(window).scroll(function(){
		top = body_dom.scrollTop();			
		filterTop = firstItem.offset().top - filter.height();
		
		if(top >= filterTop) {
			filter.addClass('fixed').css('top', 0);
			itemContent.addClass('pt83');
		}else{
			filter.removeClass('fixed').css('top', 0);
			itemContent.removeClass('pt83');
		}
	});
}

function filter_slide_to(swiper, active_slider, swiper_box, time) {
	var swiper_width_half = parseInt(swiper_box.width() / 2, 10);
	var offset_left       = active_slider.offsetLeft;
	var max_translate     = swiper.maxTranslate();
	var min_translate     = swiper.minTranslate();
	var translate_to      = swiper_width_half - offset_left - active_slider.offsetWidth / 2;

	if(translate_to > min_translate) {
		translate_to = min_translate;
	}else if(translate_to < max_translate) {
		translate_to = max_translate;
	}
	swiper.setWrapperTransition(time);
	swiper.setWrapperTranslate(translate_to);
}

module.exports = main;


