import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';

var anima_scroll, view_scroll, slideNavBox, go_top_btn;

function start() {
	go_top_btn   = $('#go-top');
	slideNavBox = $('#nav-content');
	anima_scroll = VARIABLE.GLOBAL.SCROLL_OBJ;
	view_scroll = $('#scroller');

	if(typeof flexibleConfig == 'undefined') return;
	slideNavBox.removeClass('none');
	$('#nav-content .slide-btn').click(function() {
		if(slideNavBox.hasClass('active')) {
			$('#nav-bg').addClass('none');
			slideNavBox.removeClass('active');
			$(this).removeClass('show');
		}else{
			$('#nav-bg').removeClass('none');
			slideNavBox.addClass('active');
			$(this).addClass('show');
		}
	});

	$('#nav-bg').click(function() {
		$('#nav-bg').addClass('none');
		slideNavBox.removeClass('active');
		$(this).removeClass('show');
	});

	// 创建导航1
	creatFisrtTitle();

	// 创建导航2
	creatSecTitle();

	// 创建红包
	createRedGift();

	$('#nav-content').click(function(ev) {
		var target = $(ev.target);
		var _a = target.closest('a');

		if(_a.length) {
			window.location.href = _a.attr('href');
			return false;
		}

		if(target.hasClass('return')){
			$('#nav-bg').addClass('none');
			slideNavBox.removeClass('active');
			$('#nav-content .slide-btn').removeClass('show');

			if(go_top_btn.find('.back').hasClass('none')) {
				return;
			}
			anima_scroll.scrollTo(0, true);
			go_top_btn.addClass('none');
		}else if(target.closest('*[data-moduleid]').length) {
			var thisModuleId = target.closest('*[data-moduleid]').attr('data-moduleid');

			jumpToModule(thisModuleId);
		}
	});
}

function jumpToModule(id) {
	var module = $('.flexible-area *[data-moduleid="'+ id +'"]');

	if(!module.length) return;
	var scrollTo = module.offset().top - view_scroll.offset().top;
	anima_scroll.scrollTo(scrollTo, true);

	$('#nav-bg').addClass('none');
	slideNavBox.removeClass('active');
	$('#nav-content .slide-btn').removeClass('show');

	if(go_top_btn.find('.back').hasClass('none')) {
		return;
	}
}

function createRedGift() {
	if(!flexibleConfig.redGift.picUrl) return;

	$('#nav-content .red-gift').attr('data-moduleid', flexibleConfig.redGift.id).append('<img src="'+ flexibleConfig.redGift.picUrl +'">');
}

function creatFisrtTitle() {
	var ul = $('#nav-content .first-ul');
	var titleArr = flexibleConfig.titleFirst;

	for(var i = 0; i < titleArr.length; i++) {
		var html = '';

		if(titleArr[i].href) {
			html = '<li data-moduleid="'+ titleArr[i].id +'"><a href="'+ titleArr[i].href +'">'+ titleArr[i].name +' ></a></li>';
		}else{
			html = '<li data-moduleid="'+ titleArr[i].id +'"><span>'+ titleArr[i].name +' ></span></li>';
		}

		ul.append(html);
	}
}

function creatSecTitle() {
	var ul = $('#nav-content .sec-ul');
	var ulLast = $('#nav-content .sec-last');
	var titleArr = flexibleConfig.titleSec;

	for(var i = 0; i < titleArr.length; i++) {
		var html = '';

		if(titleArr[i].href) {
			html = '<li data-moduleid="'+ titleArr[i].id +'"><a href="'+ titleArr[i].href +'">'+ titleArr[i].name +'</a></li>';
		}else{
			html = '<li data-moduleid="'+ titleArr[i].id +'"><span>'+ titleArr[i].name +'</span></li>';
		}

		ul.append(html);
	}

	for(var i = 0; i < flexibleConfig.titleLast.length; i++) {
		var html = '';

		if(flexibleConfig.titleLast[i].href) {
			html = '<li data-moduleid="'+ flexibleConfig.titleLast[i].id +'"><a href="'+ flexibleConfig.titleLast[i].href +'">'+ flexibleConfig.titleLast[i].name +'</a></li>';
		}else{
			html = '<li data-moduleid="'+ flexibleConfig.titleLast[i].id +'"><span>'+ flexibleConfig.titleLast[i].name +'</span></li>';
		}

		ulLast.append(html);
	}
}

module.exports = start;


