import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';

function mapArea() {
	if(VARIABLE.CONFIG.VS == 'jdapp') return;
	
	$('.innerWraper').bind('tap', function(ev) {
		var target = $(ev.target);
        var _href;

        if(target.get(0).tagName.toLowerCase() == 'area') {
            _href = target.attr('href');

            window.location.href = _href;
            return false;
        }
	});
}

module.exports = mapArea;


