import $ from 'ZEPTO_PATH';
import getMustBuyPrice from 'MODULES_PATH/getMustBuyPrice';

var UTIL = require('UTILS_PATH/helper');
var model = require('UNIT_PATH/model');
var view = require('UNIT_PATH/view');

function init() {
	var timerBox = $('.seckill-timer');
	var disTime = parseInt($('.seckill-timer').attr('data-time'), 10);

	timerRun();

	var timer = setInterval(timerRun, 1000);

	function timerRun() {
		var time_data = UTIL.timeTransform(disTime / 1000);

		// 时
		$('.hour', timerBox).eq(0).text(time_data.hour);

		// 分
		$('.minute', timerBox).eq(0).text(time_data.min);

		// 秒
		$('.second', timerBox).eq(0).text(time_data.sec);

		disTime -= 1000;
		if(disTime <= 0) {
			clearInterval(timer);
			timer = null;
 			refreshMustbuy();
		}
	}
}

// 刷新超值抢购的商品
function refreshMustbuy() {
	model.refreshMustbuy(function(data) {
		view.refreshMustbuy({
			data: data,
			update_sli: function() {
				init();
				$('.top-price').addClass('none');
				// 刷新新商品价格
				getMustBuyPrice();
			}
		});
	});
}

module.exports = {
	init: init
}


