import $ from 'ZEPTO_PATH';
var VARIABLE = require('UNIT_PATH/variable'); 
var model = require('UNIT_PATH/model');
var view = require('UNIT_PATH/view');

function getPrice() {
    var topImgs = $('#top-imgs .img-item');
    var priceTxt = $('.top-price .price-txt');
    var skus = '';

    if(!topImgs.length) return;

    topImgs.each(function(i, v) {
        skus += $(this).attr('data-skuid') + ',';
    });

    model.getPriceData(skus, function(data) {
        view.renderTodayPrice(data);
        $('.top-price').removeClass('none');
        if(VARIABLE.CONFIG.PRICE_SWITCH == 'off') {
            priceTxt.html('暂无价格');
        }else{
            priceTxt.html($('#top-imgs img.active').attr('data-price'));
        }
    });
}

module.exports = getPrice;


