import $               from 'ZEPTO_PATH';
import VARIABLE        from 'UNIT_PATH/variable';
import header          from 'MODULES_PATH/header.js';
import layout          from 'MODULES_PATH/layout';
import navSlider       from 'MODULES_PATH/navSlider';
import redSlider       from 'MODULES_PATH/redSlider';
import mustBuyTimer    from 'MODULES_PATH/mustBuyTimer';
import mustBuySlider   from 'MODULES_PATH/mustBuySlider';
import getMustBuyPrice from 'MODULES_PATH/getMustBuyPrice';
import hot             from 'MODULES_PATH/hot';
import FooterAd        from 'MODULES_PATH/footerAd';
import floatAd         from 'MODULES_PATH/floatAd';
import scrollMock      from 'MODULES_PATH/scrollMock';
import goTop           from 'MODULES_PATH/goTop';
import LazyLoad        from 'MODULES_PATH/indexLazyLoad';
import brandSlider     from 'MODULES_PATH/brandSlider';
import flexibleArea    from 'MODULES_PATH/flexibleArea';
import slideNav        from 'MODULES_PATH/slideNav';
import iosClickFix     from 'MODULES_PATH/iosClickFix';
import footBar         from 'MODULES_PATH/footBar';
import indexReturnFix  from 'MODULES_PATH/indexReturnFix';
import uRedFilterSlider from 'MODULES_PATH/uRedFilterSlider';

window.$ = $;

// 页面布局调整
layout();

// 类型选择滑动
uRedFilterSlider();

// 回到顶部按钮
goTop();

// 懒加载
LazyLoad();

// 公共头部
header.headerInit();

// 轮播图
redSlider.init();

// 尾部广告
var footerAd = new FooterAd();

footerAd.init();

// 自定义区域缩放
flexibleArea($('.flexible-area-inner'), function(_this) {
	_this.removeClass('op0').removeClass('none');
	_this.closest('.flexible-area-con').find('.load-wrap').remove();
	VARIABLE.GLOBAL.SCROLL_OBJ && VARIABLE.GLOBAL.SCROLL_OBJ.refresh();
});

// ios下自定义区域热连接点击延迟修复
iosClickFix();

// 底部导航
footBar();

// 页面返回锚点定位
indexReturnFix();


